import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import Header from "./HeaderContainer";
import DetailHeader from "../components/detail/header"
import Comment from "../components/comment"
import api from "../utils/api"
import '../styles/notice.scss';
import backImg from '../images/global/btn-back@2x.png';

class NoticeDetailContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      content: {
        content: "",
        created_at: "",
        images: [],
        pinned: false,
        title: "",
        written_by: "",
        comments: []
      },
      comment: "",
      notice_id: window.location.getParameterByName('id'),
    };
    this.changeHandler = this.changeHandler.bind(this);
    this.submitComment = this.submitComment.bind(this);
    this.getApi = this.getApi.bind(this);
    this.goBack = this.goBack.bind(this);
  }

  componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('공지사항');
    viewMenu(false);
    this.getApi();
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  async getApi() {
    const {notice_id} = this.state;
    const response = await api('get',`/api/v1/notice/${notice_id}`);
    this.setState({
      content: response.data.data,
      comment: "",
    });
  }

  goBack(){
    const {history} = this.props;
    history.push('/board');
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  async submitComment() {
    const {comment, notice_id} = this.state;
    const postData = {
      content: comment
    }
    const response = await api('post', `/api/v1/notice/${notice_id}/comments`, postData);
    if(response.status === 201) {
      swal('댓글이 등록되었습니다');
      this.getApi();
    } else {
      swal('댓글 등록에 실패했습니다');
    }
  }

  render() {
    const {content, comment} = this.state;
    return (
      <div className="notice-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className="detail-container">
          <DetailHeader board={content} />
          <div className="detail-content">{content.content}</div>
          <div className="comment-container">
            <div className="title">
              <span>댓글 </span>
              {content.comments.length}
            </div>
            <div className="comment-body">
              <table>
                <colgroup>
                  <col width='56' />
                  <col width='auto' />
                </colgroup>
                <tbody>
                  {
                    content.comments.map((item, index) =>
                      <Comment key={index} comment={item} />
                    )
                  }
                </tbody>
              </table>
            </div>
          </div>
          <div className="comment-inputbox">
            <input className="comment-input" type="text" placeholder="댓글달기" name="comment" onChange={this.changeHandler} value={comment} />
            <button className="register-button" type="button" onClick={this.submitComment}>전송</button>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(NoticeDetailContainer);

