import axios from "axios";
/* eslint no-use-before-define: "off", no-underscore-dangle: "off" */

const instance = axios.create({
  baseURL: process.env.NODE_ENV === "development" ? "http://ec2-13-125-107-36.ap-northeast-2.compute.amazonaws.com" 
  : "https://http://awesome-school.net",
  withCredentials: false,
  headers: {'Authorization': 'Token 2222'},
  // validateStatus: status => (status >= 200 && status < 600)
});

const api = function(method, url, data, token) {
  if (token) {
    return instance({
      method,
      url,
      data,
    });
  }
  return instance({
    method,
    url,
    data
  });
};

export default api;
