import * as ActionTypes from "../actions/common";

const initialState = {
  title: "",
  sidebarOpen: false,
  viewMenu: true,
  token: '',
  userInfo: {
    name: '',
    nickname: '',
    profile: '',
  }
};

export default (state = initialState, action) => {
  const data = action.payload;
  switch (action.type) {
    case ActionTypes.CHANGE_TITLE:
      return { ...state, title: data };
    case ActionTypes.TRIGGER_SIDEBAR:
     return { ...state, sidebarOpen: state.sidebarOpen };
    case ActionTypes.VIEW_MENU:
      return { ...state, viewMenu: data}
    case ActionTypes.SAVE_TOKEN:
      return { ...state, token: data}
    case ActionTypes.SAVE_PROFILE:
      return { ...state, userInfo: data}
    default:
      return state;
  }
};
