import React, { Component } from 'react';
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { changeTitle } from "../actions/common";

import Header from "./HeaderContainer";
import Card from "../components/board/card"
import api from "../utils/api"
import '../styles/board.scss';

class BoardContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      boards: [],
    };
  }

  async componentDidMount() {
    const { changeHeader } = this.props;
    changeHeader('게시판');
    try {
      const response = await api('get','/api/v1/community');
      this.setState({boards: response.data.data});
    } catch(e) {
      alert('게시판 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    }
  }

  render() {
    const {boards} = this.state;
    return (
      <div className="board-container">
        <Link to="/board/write">
          <div className="register-btn on">글쓰기</div>
        </Link>
        <div className="board-list">
          {
            boards.map((board, index) =>
              <Card key={index} board={board} />
            )
          }
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BoardContainer);
