import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import Header from "./HeaderContainer";
import ClassCard from "../components/class/card";
import api from "../utils/api";
import { classTime } from "../utils/static";
import backImg from '../images/global/btn-back@2x.png';

import '../styles/classdetail.scss';

class ClassDetailContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      content: {
        school_address: "",
        dates: [],
      },
      uuid: window.location.getParameterByName('id')
    };
    this.requestClass = this.requestClass.bind(this);
    this.goBack = this.goBack.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    const { uuid } = this.state;
    const response = await api('get', `/api/v1/mentor/education/${uuid}`);
    console.log("response : ", response);
    changeHeader('모집 중 수업 상세');
    viewMenu(false);
    this.setState({content: response.data.data[0]});
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  async requestClass() {
    const { uuid } = this.state;
    const { history } = this.props;
    const response = await api('post',`/api/v1/mentor/education/${uuid}/apply`);
    if(response.status < 300) {
      swal('성공적으로 신청되었습니다');
      history.push('/class');
    } else {
      swal('신청에 실패했습니다');
    }
  }

  goBack(){
    const {history} = this.props;
    history.push('/class');
  }

  render() {
    const {content} = this.state;
    return (
      <div className="classdetail-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className="class-container">
          <div className="detail-header">수업 상세 정보</div>
          <table className="detail-table">
            <tbody>
              <tr>
                <td className="left-td">지역구분</td>
                <td className="right-td">{content.region}</td>
              </tr>
              <tr>
                <td className="left-td">상세주소</td>
                <td className="right-td">{content.school_address.replace(/(<([^>]+)>)/ig,"")}</td>
              </tr>
              <tr>
                <td className="left-td">학교</td>
                <td className="right-td">{content.school}</td>
              </tr>
              <tr>
                <td className="left-td">학년</td>
                <td className="right-td">
                  {content.education_target}
                  학년
                </td>
              </tr>
              <tr>
                <td className="left-td">학급수</td>
                <td className="right-td">
                  {content.school_classes}
                  학급
                </td>
              </tr>
              <tr>
                <td className="left-td">총 학생 수</td>
                <td className="right-td">{content.total_student_number}</td>
              </tr>
              <tr>
                <td className="left-td">인터뷰</td>
                <td className="right-td">{content.interview}</td>
              </tr>
              <tr>
                <td className="left-td">기타사항</td>
                <td className="right-td">{content.order_option_comment}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="class-container">
          <div className="detail-header">수업 시간</div>
          <div className="classlist-wrapper">
            {
              content.dates.map((item, index) => (
                <div key={index} className="class-wrapper">
                  <div className="school-name">{content.school}</div>
                  <div className="class-info">
                    #
                    {item.inning}
                    회차 (
                    {item.date}
                    ) 
                    {` ${classTime(item.start_time)} `}
                    -
                    {` ${classTime(item.end_time)}`}
                  </div>
                </div>
              ))
            }
          </div>
        </div>
        <div className="divider" />
        <button className="report-button" type="submit" onClick={this.requestClass}>교육 신청하기</button>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ClassDetailContainer);

