import React from "react";
import { Link } from 'react-router-dom'
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

const HeaderComponent = ({type, dropdownOpen, toggle, filterData, optionList}) => (
  <Dropdown size="sm" isOpen={dropdownOpen} toggle={()=>toggle(type)}>
    <DropdownToggle caret>
      {optionList.optionTitle}
    </DropdownToggle>
    <DropdownMenu>
      {
        optionList.optionArr.map((item, index) => (
          <DropdownItem key={index} onClick={()=>filterData(`${item.link ? `${optionList.optionKey}=${item.link}` : ''}`)}>{item.name}</DropdownItem>
        ))
      }
    </DropdownMenu>
  </Dropdown>
)

export default HeaderComponent
