import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class FeedbackContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      feedback: undefined,
    };
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('수업 후 강사 의견');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('게시판 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  clickOption() {
    const {feedback} = this.state;
    this.setState({feedback: !feedback })
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  render() {
    const {feedback} = this.state;
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className={`register-btn ${feedback !== undefined ? "on" : ""}`} onClick={this.postApi}>등록</div>
        <div className="content">
          <div className="desc">
            수업 피드백 설문을 작성해주세요.
            <br />
            작성할 피드백이 없는 경우 
            <br />
            ‘피드백 없음’ 버튼을 선택해주세요.
          </div>
          <div className={`review-option ${feedback === true ? "on" : ""}`} onClick={this.clickOption}>구글 드라이브에 업로드</div>
          <div className={`review-option ${feedback === false ? "on" : ""}`} onClick={this.clickOption}>피드백 없음</div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FeedbackContainer);
