import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class SurveyContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      boards: [],
      title: '',
      content: '',
    };
    this.goBack = this.goBack.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('학생 설문조사');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('게시판 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  render() {
    const {boards, title, content} = this.state;
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className={`register-btn ${title && content ? "on" : ""}`} onClick={this.postApi}>등록</div>
        <div className="content">
          <div className="desc">학생 설문조사를 작성해주세요.</div>
          <button type="submit" className="upload-btn">구글 드라이브에 업로드</button>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SurveyContainer);
