import React, { Component } from 'react';
import { connect } from "react-redux";

import { changeTitle, viewSidemenu } from "../actions/common";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';

class CommentsContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      value: 10,
    };
    this.goBack = this.goBack.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('담당 반');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('게시판 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  handleChange(value) {
    this.setState({value: Number(value.toFixed(1))})
  };

  render() {
    const {boards, title, content, value} = this.state;
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className={`register-btn ${title && content ? "on" : ""}`} onClick={this.postApi}>등록</div>
        <div className="content">
          <div className="input-title">담당 반</div>
          <input className="input class" type="text" placeholder="담당했던 반을 입력해주세요. (예: 1반, 10반 등)" />
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CommentsContainer);
