import React, { Component } from 'react';
import { connect } from "react-redux";

import { changeTitle } from "../actions/common";
import SidebarButton from '../components/header/sidebarButton';
import sidemenu from '../images/global/btn-side-menu@2x.png'

class HeaderContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {};
  }

  componentDidMount() {
    const {token} = this.props;
    if(!token) window.location.href = "/";
  }

  render () {
    const {onSetSidebarOpen, title, viewMenu} = this.props;
    return (
      <div className="header-container">
        { 
          viewMenu ? <SidebarButton onSetSidebarOpen={onSetSidebarOpen} /> : null
        }
        <div className="title">{ title }</div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
  viewMenu: common.viewMenu,
  token: common.token,
});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(HeaderContainer);
