import React, { Component } from 'react';
import { connect } from "react-redux";
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css'

import { changeTitle, viewSidemenu } from "../actions/common";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';

const horizontalLabels = {
  0: '0',
  100: '10',
}

class SatisfactionContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      value: 10,
    };
    this.goBack = this.goBack.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('평균 만족도 점수');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('게시판 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  handleChange(value) {
    this.setState({value: Number(value.toFixed(1))})
  };

  render() {
    const {boards, title, content, value} = this.state;
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className={`register-btn ${title && content ? "on" : ""}`} onClick={this.postApi}>등록</div>
        <div className="content">
          <div className="desc">수업 만족도를 선택해주세요</div>
          <div className='point'>
            <span className="num">{value}</span>
            <span className="unit">점</span>
          </div>
          <Slider
            min={0}
            max={10}
            step={0.1}
            value={value}
            labels={horizontalLabels}
            onChange={this.handleChange}
          />
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SatisfactionContainer);
