import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';
import addPlusImage from '../images/board/btn-plus-pic@2x.png';

const onAttachImg = (index) => {
  document.getElementById(`attachBtn${index}`).click();
}

const receiptList = [
  {
    title: "재료비 영수증",
    name: "item_cost",
  },
  {
    title: "교통비 영수증",
    name: "transport_cost",
  },
  {
    title: "숙박비",
    name: "room_cost",
  },
  {
    title: "식비",
    name: "food_cost",
  },
  {
    title: "기타(미팅, 보험)",
    name: "etc_cost",
  },
]

class ReviewContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      item_cost: 0,
      transport_cost: 0,
      room_cost: 0,
      food_cost: 0,
      etc_cost: 0,
      images: {
        item_cost: false,
        transport_cost: false,
        room_cost: false,
        food_cost: false,
        etc_cost: false,
      }
    };
    this.goBack = this.goBack.bind(this);
    this.addImage = this.addImage.bind(this);
    this.onBlurHandler = this.onBlurHandler.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('정산 신청');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('게시판 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  onBlurHandler(e) {
    if(!e.target.value) this.setState({[e.target.name]: 0});
  }

  onChangeHandler(e) {
    const onlyNumber = /^(\d|[^-]-)+$/;
    let targetValue = e.target.value;
    if(targetValue) {
      if(onlyNumber.test(e.target.value)) {
        while (targetValue[0] === '0' && targetValue.length > 1) {
          targetValue = targetValue.slice(1, targetValue.length);
        }
        this.setState({[e.target.name]: targetValue});
      } else {
        alert('번호만 입력해주세요')
      }
    } else {
      this.setState({[e.target.name]: targetValue});
    }
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  addImage(event) {
    event.persist()
    const {images} = this.state;
    console.log(event.target.name)
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();  
      reader.onload = (e) => {
        images[event.target.name] = e.target.result;
        this.setState({ images });
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  render() {
    const {item_cost, transport_cost, room_cost, food_cost, etc_cost} = this.state;
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>취소</div>
        <div className="content">
          <div className="desc">
            <span>수업 피드백 강사의견을 선택해주세요.</span>
            <p className="sub-desc">(중복 선택 가능)</p>
          </div>
          <div className="cost-container">
            {
              receiptList.map((item, index) => (
                <div className="cost-wrapper" key={index}>
                  <div className="input-wrapper">
                    <span className="input-label">{item.title}</span>
                    <span className="input-unit">원</span>
                    <input className="cost-input" type="number" onChange={this.onChangeHandler} onBlur={this.onBlurHandler} name={item.name} value={this.state[item.name]} />
                  </div>
                  <div className={`image-addbox ${this.state.images[item.name] ? "on" : ""}`} name={item.name} style={{backgroundImage: `url("${this.state.images[item.name] ? this.state.images[item.name] : addPlusImage}")`}} onClick={() => onAttachImg(index)} />
                  <input id={`attachBtn${index}`} className="hide-button displaynone" name={item.name} type='file' accept="image/*" onChange={this.addImage} />
                </div>
              ))
            }
          </div>
        </div>
      </div>
    )
  }
}

// item_cost, transport_cost, room_cost, food_cost, etc_cost
// {
//   "item_needed" = "<재료비>",
//    "transport_cost" = "<교통비>",
//    "accommodation_exp" = "<숙박비>", 
//    "food_exp" = "<식비>",
//    "others" = "<기타>"
// }

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ReviewContainer);
